
.features-page {

    margin-top: 64px;
    padding: 0 8rem; 
    padding-top: 3rem;
    padding-bottom: 3rem;
    min-height: 100vh;
    background: #fff; 

    h1._title {
        font-size: 2.75rem;
        font-weight: 900;        
        margin-bottom: 3rem;
    }

    ._title {
        font-size: 2.15rem;
        font-weight: 500;

        background: #111;
        color: #fff;

        background: #fff;
        color: #222;

        padding: 0px 0.5rem;
        margin-bottom: 1rem;
    }

    ._description {
        font-size: 1rem;        
        // background: #f4f4f4;
        margin-bottom: 2rem;
    }

    .feature-group {
        line-height: 250%;

        .feature-group-title {
            font-weight: 900;
            font-size: 1.15rem;
            margin-right: 0.25rem;
            color: #444; 
            // background: #f4f4f4; 
        }
        .feature-item {
            font-size: 1rem;
            color: #000; 
            background: #f4f4f4; 
            // border: 1px solid rgba(0,0,0,0.1); 
            box-shadow: 1px 1px 2px 0 rgba(0, 0, 0, 0.075);
            padding: 0.2rem 0.25rem;
            margin: 0 0.25rem;
            border-radius: 0.2rem; 
            font-family: 'Courier New', monospace;            
        }    
    }

    .feature-group2 {
        h2 {
            font-weight: bold;
        }
    }

    ul {
        font-size: 1.1rem;
        list-style: disc;
        li {
            margin-left: 1.4rem;
        }
    }
}