$width: 25rem;

.docs-page {

    ._left {
        min-width: $width;
    }
    ._right {}
    .docs-content {
        padding: 0 8rem; 
        padding-bottom: 4rem;
        min-height: 100vh; 
        max-width: 800px;     
        
        h1 {
            font-size: 2.5rem;
            font-weight: 900;;
        }
        h2 {
            font-size: 2rem;
            font-weight: 600;
        }
        h3 {
            font-size: 1.5rem;
            font-weight: 600;
        }
    }

    #markdown-toc {
        position: fixed;
        top: 42px;
        left: 0;
        padding-top: 1rem;
        padding-left: 2rem;
        width: $width;
        // background: #eee;
        overflow-y: scroll;
        max-height: calc(100vh - 42px);
        padding-bottom: 200px;
        box-sizing: border-box;
    }

    ul#markdown-toc,
    #markdown-toc ul {
        font-size: 1rem;
        list-style: none;

        li {
            margin-left: 1.4rem;
            a:not(:hover) {
                text-decoration: none;
            }
        }
    }
}