.release-notes-page {

    max-width: 600px;
    margin-left: 2rem;
    margin-right: 2rem;

    h1 {
        font-size: 2.25rem;
        font-weight: 500;
    }

    .release {
        margin-top: 1rem;
        margin-bottom: 1rem;

        ._version {
            font-size: 1.5rem;
            font-weight: 500;
            margin-bottom: 0.25rem;
        }
    
        ._date {
            font-size: 1.25rem;
            font-weight: 500;
        }
    
        ul {
            font-size: 1.1rem;
            list-style: disc;
            li {
                margin-left: 1.4rem;
            }
        }
    }    
}