
.grid1 {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: min-content min-content min-content min-content min-content min-content;
    grid-gap: 1rem;
    grid-template-areas: 
    "h1 h1 img1"
    "card1 card2 img1"
    "card3 card4 card5"
    "card6 card7 card8"
    "card9 footer footer"
    "card10 footer footer";
}


.grid2 {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: min-content min-content;
    grid-gap: 1rem;
    grid-template-areas: 
    "it1 it2"
    "it3 it4";
}

.lottie-page {

    max-width: 1400px;
    margin-left: 2rem;
    margin-right: 2rem;

    font-size: 1rem;    

    ._title {
        font-size: 2.5rem;
        font-weight: 900;
        // background: #111;
        color: #111;
        padding: 0px 0.5rem;
    }

    h2 {
        font-size: 1.5rem;
        font-weight: 600;
    }

    h3 {
        font-size: 2rem;
        font-weight: 600;
        color: #111;
    }

    .lottie-feature-group {
        overflow: hidden;
        background: #fff;
        box-shadow: 1px 1px 16px rgba(0, 0, 0, 0.1);

        .lottie-feature-title {
            font-size: 1.25rem;
            font-weight: 900;
            height: 50px;
            color: #111;
            z-index: 1;
            padding-left: 1rem;
        }

        .lottie-feature-content {
            padding: 1rem;
            line-height: 150%;
        }

        .lottie-feature-icon {
            position: absolute;
            top: 50%;
            right: 16px;
            transform: translate(0, -40%)scale(1.75, 1.75);
            // fill: #444; 
            z-index: 1;
        }
    }

    .lottie-card2 {
        width: 400px;
    }

    ._block {
        position: relative;
        // border: 4px solid #444;
        // background: #fff8f3;
        background: #fff;
        color: #666;
        font-size: 1.25rem;
        // border-radius: 1rem;
        padding: 1.5rem;
        box-shadow: 1px 1px 16px rgba(0, 0, 0, 0.1);

        &::after {
            content: "";
            position: absolute;
            inset: 8px;
            border: 1px dashed hsl(211, 100%, 90%);
        }
    }

    ._block2 {
        // border: 4px solid #444;
        // background: #111;
        // color: #fff;
        font-size: 1.25rem;
        // border-radius: 1rem;
        padding: 1rem;
        margin: 1rem;
    }

    ._small-block {
        border: 2px solid #444;
        background: #fff;
        box-shadow: 0 4px 5px 0 rgba(0, 0, 0, 0.14), 0 1px 10px 0 rgba(0, 0, 0, 0.12), 0 2px 4px -1px rgba(0, 0, 0, 0.4);
        // border-radius: 2rem;
        padding: 1rem;
        margin: 1rem;
        max-width: 20rem;
        line-height: 150%;
    }
}

.lottie-stacked-paper {
    width: 400px;
    margin: 4rem;
}


.stacked-paper {
    background: #fff;
    padding: 30px;
    position: relative;
}

.stacked-paper,
.stacked-paper::before,
.stacked-paper::after {
    /* Styles to distinguish sheets from one another */
    box-shadow: 1px 1px 16px rgba(0, 0, 0, 0.35);
}

.stacked-paper::before,
.stacked-paper::after {
    content: "";
    position: absolute;
    height: 102%;
    width: 102%;
}

.stacked-paper::after {
    background-color: hsl(211, 100%, 70%);
    right: 5px;
    top: -2px;
    transform: rotate(-3deg);
    z-index: -2;
}

.stacked-paper::before {
    background-color: hsl(211, 100%, 80%);
    top: 2px;
    right: -4px;
    transform: rotate(3deg);
    z-index: -1;
}

.stacked-paper-red {
    &.stacked-paper::after {
        background-color: hsl(0, 100%, 70%);
    } 
    &.stacked-paper::before {
        background-color: hsl(0, 100%, 80%);
    }       
}

.stacked-paper-purple {
    &.stacked-paper::after {
        background-color: hsl(240, 100%, 70%);
    } 
    &.stacked-paper::before {
        background-color: hsl(240, 100%, 80%);
    }       
}

.stacked-paper-green {
    &.stacked-paper::after {
        background-color: hsl(160, 100%, 70%);
    } 
    &.stacked-paper::before {
        background-color: hsl(160, 100%, 80%);
    }       
}


.laptop-card {
    position: relative;
    // border: 2px solid rgba(0,0,0,0.2);
    background: #fff;
    border-radius: 12px;
    padding: 1rem;
    margin: 3rem;

    box-shadow: 2px 2px 8px rgba(0, 0, 0, 0.25);

    ._content {
        h3 {
            margin-top: 1rem;
            margin-bottom: 1rem;
        }
        position: relative;
        z-index: 1;
        line-height: 180%;
    }

    border: 1px solid hsl(210, 69%, 85%);
    &::before { background: hsl(210, 69%, 40%); }

    &._red {
        border: 1px solid hsl(0, 69%, 85%);
        &::before { background: hsl(0, 69%, 40%); }    
    }

    &._green {
        border: 1px solid hsl(180, 69%, 85%);
        &::before { background: hsl(140, 69%, 30%); }    
    }

    &::before {
        content: "";
        position: absolute;
        top: calc(100% + 16px);
        left: -16px;
        height: 20px;
        width: calc(100% + 32px);
        border-bottom-left-radius: 16px;
        border-bottom-right-radius: 16px;
        border-top-left-radius: 2px;
        border-top-right-radius: 2px;        
        box-shadow: 2px 2px 8px rgba(0, 0, 0, 0.5);
    }

    &::after {
        content: "";
        position: absolute;
        top: calc(100% + 16px);
        left: 50%;
        height: 12px;
        width: 30%;
        transform: translate(-50%,0);
        border-bottom-left-radius: 16px;
        border-bottom-right-radius: 16px;
        border-top-left-radius: 0;
        border-top-right-radius: 0;
        background: rgba(255,255,255,0.9);
        box-shadow: inset 0 0 4px rgba(0, 0, 0, 0.45);
    }
}